type SortKey = keyof Customer | null;
type SortDirection = "ascending" | "descending";

interface SortConfig {
  key: SortKey;
  direction: SortDirection;
}

interface ColumnConfig {
  id: keyof Customer | 'actions';
  label: string;
  canHide: boolean;
  sortable: boolean;
  minWidth?: string;
  className?: string; 
  headerClassName?: string;
}

const columnsConfig: ColumnConfig[] = [
  { id: 'avatarUrl', label: 'Avatar', canHide: true, sortable: false, minWidth: "80px", headerClassName: "w-[80px]" },
  { id: 'name', label: 'Name', canHide: false, sortable: true, minWidth: "150px" },
  { id: 'email', label: 'Email', canHide: true, sortable: true, minWidth: "200px" },
  { id: 'totalOrders', label: 'Orders', canHide: true, sortable: true },
  { id: 'totalSpent', label: 'Total Spent', canHide: true, sortable: true, minWidth: "120px", className: "text-right" },
  { id: 'status', label: 'Status', canHide: true, sortable: true },
  { id: 'joinedDate', label: 'Joined Date', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'actions', label: 'Actions', canHide: false, sortable: false, className: "text-right", minWidth: "130px" },
];

function AdminCustomersPageContent() {
  const [customers, setCustomers] = useState<Customer[]>(initialCustomers);
  const [sortConfig, setSortConfig] = useState<SortConfig>({ key: null, direction: 'ascending' });
  
  const { isNewCustomerSheetOpen, setIsNewCustomerSheetOpen } = useCustomerActions();

  const initialVisibility = columnsConfig.reduce((acc, col) => {
    acc[col.id as string] = true;
    return acc;
  }, {} as Record<string, boolean>);
  const [columnVisibility, setColumnVisibility] = useState<Record<string, boolean>>(initialVisibility);

  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState<"All" | "Active" | "Inactive">("All");
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageInput, setPageInput] = useState(currentPage.toString());

  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [customerToDelete, setCustomerToDelete] = useState<Customer | null>(null);

  useEffect(() => {
    setPageInput(currentPage.toString());
  }, [currentPage]);

  const handlePageInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPageInput(e.target.value);
  };

  const handlePageInputSubmit = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      const newPage = parseInt(pageInput, 10);
      if (!isNaN(newPage) && newPage >= 1 && newPage <= totalPages) {
        setCurrentPage(newPage);
      } else {
        setPageInput(currentPage.toString()); 
      }
    }
  };

  const filteredCustomers = useMemo(() => {
    return customers.filter(customer => {
      const searchLower = searchTerm.toLowerCase();
      const matchesSearchTerm =
        customer.name.toLowerCase().includes(searchLower) ||
        customer.email.toLowerCase().includes(searchLower);
      const matchesStatus =
        statusFilter === 'All' || customer.status === statusFilter;
      return matchesSearchTerm && matchesStatus;
    });
  }, [customers, searchTerm, statusFilter]);

  const sortedCustomers = useMemo(() => {
    let sortableItems = [...filteredCustomers];
    if (sortConfig.key !== null) {
      sortableItems.sort((a, b) => {
        const aVal = a[sortConfig.key!];
        const bVal = b[sortConfig.key!];
        
        if (typeof aVal === 'number' && typeof bVal === 'number') {
          return sortConfig.direction === 'ascending' ? aVal - bVal : bVal - aVal;
        }
        if (typeof aVal === 'string' && typeof bVal === 'string') {
          return sortConfig.direction === 'ascending' ? aVal.localeCompare(bVal) : bVal.localeCompare(aVal);
        }
        return 0;
      });
    }
    return sortableItems;
  }, [filteredCustomers, sortConfig]);

  const totalPages = useMemo(() => {
    return Math.max(1, Math.ceil(sortedCustomers.length / rowsPerPage));
  }, [sortedCustomers.length, rowsPerPage]);

  const paginatedCustomers = useMemo(() => {
    const startIndex = (currentPage - 1) * rowsPerPage;
    const endIndex = startIndex + rowsPerPage;
    return sortedCustomers.slice(startIndex, endIndex);
  }, [sortedCustomers, currentPage, rowsPerPage]);


  const requestSort = (key: SortKey) => {
    if (!key) return;
    let direction: SortDirection = 'ascending';
    if (sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  };

  const getSortIcon = (key: SortKey) => {
    if (sortConfig.key !== key) {
      return <ArrowUpDown className="ml-2 h-4 w-4 opacity-50" />;
    }
    return sortConfig.direction === 'ascending' ? 
      <ArrowUpDown className="ml-2 h-4 w-4 text-primary" /> : 
      <ArrowUpDown className="ml-2 h-4 w-4 text-primary" />; 
  };

  const toggleColumnVisibility = (columnId: string) => {
    setColumnVisibility(prev => ({ ...prev, [columnId]: !prev[columnId] }));
  };

  const handleDeleteClick = (customer: Customer) => {
    setCustomerToDelete(customer);
    setShowDeleteConfirm(true);
  };

  const handleConfirmDelete = () => {
    if (customerToDelete) {
      setCustomers(prevCustomers => prevCustomers.filter(c => c.id !== customerToDelete.id));
    }
    setShowDeleteConfirm(false);
    setCustomerToDelete(null);
  };
  
  const handleViewCustomer = (customer: Customer) => {
    // Placeholder for view action
    console.log("Viewing customer:", customer.name);
  };

  const handleEditCustomer = (customer: Customer) => {
    // Placeholder for edit action
    console.log("Editing customer:", customer.name);
  };
  
  const handleAddNewCustomer = (data: CustomerFormData) => {
    const initials = data.name.split(' ').map(n => n[0]).join('').toUpperCase().slice(0,2);
    const newCustomer: Customer = {
      id: `CUST${Date.now()}`, // Simple unique ID
      name: data.name,
      email: data.email,
      status: data.status,
      avatarUrl: `https://placehold.co/40x40.png?text=${initials}`,
      avatarFallback: initials,
      totalOrders: 0,
      totalSpent: 0,
      joinedDate: new Date().toISOString().split('T')[0], // YYYY-MM-DD
    };
    setCustomers(prevCustomers => [newCustomer, ...prevCustomers]);
    setIsNewCustomerSheetOpen(false); // Close the sheet
  };

  return (
    <div className="space-y-8">
      <Card className="shadow-md w-full mx-auto">
        <CardHeader className="p-2.5">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-x-4 gap-y-2">
            <CardTitle className="whitespace-nowrap flex-shrink-0">Customer List</CardTitle>
            
            <div className="flex flex-1 min-w-0 items-center gap-2">
              <div className="relative flex-grow">
                <SearchIcon className="absolute left-2.5 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search name or email..."
                  value={searchTerm}
                  onChange={(e) => {setSearchTerm(e.target.value); setCurrentPage(1);}}
                  className="pl-8 w-full"
                />
              </div>

              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="icon" className="flex-shrink-0">
                    <Eye className="h-4 w-4" />
                    <span className="sr-only">Toggle Columns</span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuLabel>Toggle Columns</DropdownMenuLabel>
                  <DropdownMenuSeparator />
                  {columnsConfig.filter(col => col.canHide).map((column) => (
                    <DropdownMenuCheckboxItem
                      key={column.id}
                      checked={columnVisibility[column.id as string]}
                      onCheckedChange={() => toggleColumnVisibility(column.id as string)}
                    >
                      {column.label}
                    </DropdownMenuCheckboxItem>
                  ))}
                </DropdownMenuContent>
              </DropdownMenu>

              <Select value={statusFilter} onValueChange={(value) => {setStatusFilter(value as "All" | "Active" | "Inactive"); setCurrentPage(1);}}>
                <SelectTrigger className="w-auto min-w-[40px] flex-shrink-0 p-2.5">
                  <Filter className="h-4 w-4 text-muted-foreground" />
                  <span className="sr-only"><SelectValue placeholder="Filter by Status" /></span>
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="All">All Statuses</SelectItem>
                  <SelectItem value="Active">Active</SelectItem>
                  <SelectItem value="Inactive">Inactive</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
          <CardDescription className="mt-2">
            View and manage your customer database. ({filteredCustomers.length} customers found)
          </CardDescription>
        </CardHeader>
        <CardContent className="p-0 overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                {columnsConfig.map((column) => (
                  columnVisibility[column.id as string] && (
                    <TableHead
                      key={column.id}
                      style={column.minWidth ? { minWidth: column.minWidth } : {}}
                      className={cn(
                        "text-center", 
                        column.headerClassName
                      )}
                      onClick={() => column.sortable && requestSort(column.id as keyof Customer)}
                    >
                      <div className={cn(
                        "flex items-center justify-center",
                        column.sortable ? 'cursor-pointer hover:text-foreground' : ''
                      )}>
                        <span className="whitespace-nowrap">{column.label}</span>
                        {column.sortable && getSortIcon(column.id as keyof Customer)}
                      </div>
                    </TableHead>
                  )
                ))}
              </TableRow>
            </TableHeader>
            <TableBody>
              {paginatedCustomers.map((customer) => (
                <TableRow key={customer.id}>
                  {columnVisibility['avatarUrl'] && (
                    <TableCell>
                      <Avatar className="h-10 w-10">
                        <AvatarImage src={customer.avatarUrl} alt={customer.name} data-ai-hint="person portrait"/>
                        <AvatarFallback>{customer.avatarFallback}</AvatarFallback>
                      </Avatar>
                    </TableCell>
                  )}
                  {columnVisibility['name'] && <TableCell className="font-medium">{customer.name}</TableCell>}
                  {columnVisibility['email'] && <TableCell className="text-muted-foreground">{customer.email}</TableCell>}
                  {columnVisibility['totalOrders'] && <TableCell className="text-center">{customer.totalOrders}</TableCell>}
                  {columnVisibility['totalSpent'] && <TableCell className="text-right">${customer.totalSpent.toFixed(2)}</TableCell>}
                  {columnVisibility['status'] && (
                    <TableCell className="text-center">
                      <Badge variant={customer.status === "Active" ? "secondary" : "outline"}>
                        {customer.status}
                      </Badge>
                    </TableCell>
                  )}
                  {columnVisibility['joinedDate'] && <TableCell className="text-muted-foreground">{customer.joinedDate}</TableCell>}
                  {columnVisibility['actions'] && (
                    <TableCell className="text-right">
                      <div className="flex items-center justify-end gap-1">
                        <Button
                          size="icon"
                          onClick={() => handleViewCustomer(customer)}
                          aria-label="View customer"
                          className="bg-green-500 hover:bg-green-600 text-white"
                        >
                          <Eye className="h-4 w-4" />
                        </Button>
                        <Button
                          size="icon"
                          onClick={() => handleEditCustomer(customer)}
                          aria-label="Edit customer"
                          className="bg-yellow-500 hover:bg-yellow-600 text-black"
                        >
                          <PencilLine className="h-4 w-4" />
                        </Button>
                        <Button
                          size="icon"
                          onClick={() => handleDeleteClick(customer)}
                          aria-label="Delete customer"
                          className="bg-red-500 hover:bg-red-600 text-white"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
          {paginatedCustomers.length === 0 && (
             <div className="border border-dashed border-border rounded-md p-8 text-center mt-4">
                <p className="text-muted-foreground">No customers found matching your criteria.</p>
            </div>
          )}
        </CardContent>
        <CardFooter className="flex flex-row items-center justify-between gap-4 border-t p-2.5">
          <div className="flex flex-col items-start text-sm text-muted-foreground space-y-1">
            <span className="text-xs">Rows</span>
            <Select
              value={rowsPerPage.toString()}
              onValueChange={(value) => {
                setRowsPerPage(Number(value));
                setCurrentPage(1);
              }}
            >
              <SelectTrigger className="w-[70px]">
                <SelectValue placeholder={rowsPerPage.toString()} />
              </SelectTrigger>
              <SelectContent>
                {[5, 10, 15, 20].map((pageSize) => (
                  <SelectItem key={pageSize} value={pageSize.toString()}>
                    {pageSize}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="flex flex-col items-end space-y-1">
            <span className="text-xs text-muted-foreground">
              Page {totalPages === 0 ? 0 : currentPage} of {totalPages}
            </span>
            <div className="flex items-center space-x-1">
              <Button
                variant="outline"
                size="icon"
                onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                disabled={currentPage === 1 || totalPages === 0}
                aria-label="Go to previous page"
              >
                <ChevronLeft className="h-4 w-4" />
              </Button>
              <Input
                type="number"
                value={pageInput}
                onChange={handlePageInputChange}
                onKeyDown={handlePageInputSubmit}
                className="h-9 w-14 text-center px-1"
                min="1"
                max={totalPages}
                disabled={totalPages === 0}
                aria-label="Page number input"
              />
              <Button
                variant="outline"
                size="icon"
                onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
                disabled={currentPage === totalPages || totalPages === 0}
                aria-label="Go to next page"
              >
                <ChevronRight className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </CardFooter>
      </Card>

      <AlertDialog open={showDeleteConfirm} onOpenChange={setShowDeleteConfirm}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you absolutely sure?</AlertDialogTitle>
            <AlertDialogDescription>
              This action cannot be undone. This will permanently delete the customer
              "{customerToDelete?.name}" and remove their data from our servers.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => setCustomerToDelete(null)}>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={handleConfirmDelete} className="bg-destructive hover:bg-destructive/90">
              Delete
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      <Sheet open={isNewCustomerSheetOpen} onOpenChange={setIsNewCustomerSheetOpen}>
        <SheetContent className="sm:max-w-lg p-0">
          <SheetHeader className="p-6 pb-4">
            <SheetTitle>Add New Customer</SheetTitle>
            <SheetDescription>
              Fill in the details below to add a new customer to the system.
            </SheetDescription>
          </SheetHeader>
          <div className="p-6 pt-0">
            <CustomerForm 
              onSubmit={handleAddNewCustomer} 
              onCancel={() => setIsNewCustomerSheetOpen(false)} 
            />
          </div>
        </SheetContent>
      </Sheet>
    </div>
  );
}

export default function AdminCustomersPage() {
  return (
      <AdminCustomersPageContent />
  )
}

